import { createRouter, createWebHistory } from 'vue-router';

const toRoute = (name, value, meta) => {
    if (typeof value == 'object') return { name: name, path: name, ...value, meta: { ...meta, ...value.meta }, props: true }
    else return { name: name, path: name, component: value, meta, props: true }
}

// 
let pages = {
    public: {
        '/sign-in': {
            component: () => import('@/layouts/signIn.vue'),
            children: [
                toRoute('', () => import('@/views/signIn/index.vue')),
                toRoute('register', () => import('@/views/signIn/register.vue')),
                toRoute('registerStep1', () => import('@/views/signIn/registerStep1.vue')),
                toRoute('registerStep2', () => import('@/views/signIn/registerStep2.vue')),
                toRoute('registerAD', () => import('@/views/signIn/registerAD.vue')),
                toRoute('registerADReason', () => import('@/views/signIn/registerADReason.vue')),
                toRoute('forget', () => import('@/views/signIn/forget.vue')),
                toRoute('forgetSet', () => import('@/views/signIn/forgetSet.vue')),
                toRoute('forgetSetSuccess', () => import('@/views/signIn/forgetSetSuccess.vue')),
                toRoute('registerSuccess', () => import('@/views/signIn/registerSuccess.vue')),
            ]
        },

    },
    private: {
        "/home/index": { component: () => import("@/views/home/index.vue"), meta: { noContainer: true } },
        "/users/list": () => import("@/views/users/list"),
        "/functions": () => import("@/views/functions/index.vue"),
        "/roles": () => import("@/views/roles/treeCard.vue"),
        "/roles/list": () => import("@/views/roles/list.vue"),
        "/unit/list": () => import("@/views/unit/list.vue"),
        "/role-permissions": () => import("@/views/permissions/rolePermissions.vue"),
        "/personal-permissions": () => import("@/views/permissions/personalPermissions.vue"),
        "/file-list": () => import("@/views/fileList/fileList.vue"),
        "/profile": () => import("@/views/profile/index.vue"),
        "/change-password": () => import("@/views/profile/changePassword.vue"),
    },
    dashboard: {
        "/dashboard/psychology1": () => import("@/views/dashboard/psychology/no1.vue"),
        "/dashboard/physicalFitness": () => import("@/views/dashboard/physicalFitness/index.vue"),
        "/dashboard/physiology1": () => import("@/views/dashboard/physiology/tiredBody.vue"),
        "/dashboard/nutrition1": () => import("@/views/dashboard/nutrition/water.vue"),
        "/dashboard/general1": () => import("@/views/dashboard/general/no1.vue"),
    },
    shared: {
        '/403': () => import("@/views/403"),
    }
}

export default createRouter({
    history: createWebHistory(import.meta.env.BASE_URL),
    scrollBehavior: () => ({ top: 0 }),
    routes: [
        // 
        ...Object.keys(pages.public).map(b => toRoute(b, pages.public[b], { allowAnonymous: true })),
        {
            path: "",
            component: () => import("@/layouts/default"),
            children: [
                // 
                ...Object.keys(pages.private).map(b => toRoute(b, pages.private[b])),
                { path: '', redirect: '/home/index' },
                //  / 
                ...Object.keys(pages.shared).map(b => toRoute(b, pages.shared[b], { noPermission: true })),
                //  /home/index /home/index 
                // 404
                { name: '404', path: "/:catchAll(.*)", component: () => import("@/views/404"), meta: { noPermission: true } }
            ]
        },
        {
            path: "",
            component: () => import("@/layouts/dashboard"),
            children: [
                ...Object.keys(pages.dashboard).map(b => toRoute(b, pages.dashboard[b]))
            ]
        }
    ]
});
