function rest(url, o) {
  return {
    get: (a) => axios.get(replace(url, a)),
    save: (a, b) => b ?
      axios.post(replace(url, a), b) :
      axios.post(replace(url), a),
    update: (a, b) => axios.put(replace(url, a), b),
    delete: (a, b) => b ?
      axios.delete(replace(url, a), { data: b }) :
      axios.delete(replace(url, a)),
    ...o
  }
}

function replace(url, o = {}) {
  Object.keys(o).forEach(a => {
    url = url.replace(`{/${a}}`, `/${o[a]}`)
  })
  return url.replace(/{\/[a-zA-Z]+}/g, '')
}
