<template>
  <router-view />
</template>

<script setup>
import { onMounted, ref, provide } from "vue";
import { useRouter } from "vue-router";
import { my } from "./requests";
import "jstree";
$.jstree.defaults.core.force_text = true;

let pages = [];
let apis = [];
let operations = [];
let result = null;
let router = useRouter();
const title = ref(null);
const subTitle = ref(null);

let functions = async (reset) => {
  if (reset) result = null;
  if (result) return result;

  result = await my.functions();
  pages = [];
  apis = [];
  operations = [];

  (function x(a) {
    if (!a) return;
    a.forEach((b) => {
      switch (b.type) {
        case "page":
          pages.push(b);
          break;
        case "api":
          apis.push(b);
          break;
        case "operation":
          operations.push(b);
          break;
      }
      x(b.children);
    });
  })(result.data);

  return result;
};

onMounted(() => {
  router.beforeResolve(async (to, _, next) => {
    if (to.meta.allowAnonymous === true) return next();
    if (!localStorage.getItem("token")) return next("/sign-in");
    if (new Date() - new Date(localStorage.getItem("token.time")) >= 1200000)
      return next("/sign-in");
    if (to.meta.noPermission) return next();
    await functions();
    if (to.name.toLocaleLowerCase() != "/home/index" && to.name.toLocaleLowerCase() != "/home" && !pages.map((a) => a.url).includes(to.name))
      return next("/403");
    next();
  });
});

provide("root", {
  title,
  subTitle,
  functions,
});

defineExpose({ title, subTitle });
</script>

<style>
@import "jstree/dist/themes/default/style.css";
</style>
