import dayjs from 'dayjs'
import 'dayjs/locale/zh-tw'
import relativeTime from 'dayjs/plugin/relativeTime'

//  dayjs
dayjs.extend(relativeTime)
dayjs.locale('zh-tw')

export default {
  install(app) {
    const api = {
      now: () => dayjs().format('YYYY-MM-DD HH:mm'),
      format: (v, f = 'YYYY-MM-DD HH:mm') => (v && dayjs(v).isValid()) ? dayjs(v).format(f) : null,
      fromNow: (v) => (v && dayjs(v).isValid()) ? dayjs(v).fromNow() : null,
      left: (v, d) => (v && dayjs(v).isValid()) ? dayjs(v).add(d, 'day').diff(dayjs(), 'day', true).toFixed(2) : null,
      raw: () => dayjs(),
    }
    app.config.globalProperties.$dayjs = api
  }
}